// Place all the styles related to the Users controller here.
// They will automatically be included in application.css.
// You can use Sass (SCSS) here: http://sass-lang.com/
.users {
	.row {
		@include sameHeightLineheight(3rem);
		display: flex;
		justify-content: space-around;
		+ .row {
			margin-top: .5rem;
		}
	}
	input {
		display: inline-block;
		@include sameHeightLineheight(3rem);
		border-top: none;
		border-left: none;
		border-right: none;
		border-bottom: 1px solid #999;
		&:focus {
			border-bottom: 1px solid #000;
		}
	}
	.btn-submit {
		display: block;
		margin: 0 auto;
		width: 10rem;
		padding: 0;
		background-color: #fff;
		border: 1px solid #999;
		border-radius: 4px;
	}
}
.new_user {
	border: 1px solid #999;
	padding: 1rem 0;
}
.form-container {
	margin: 0 auto;
	width: 40rem;
}
.row {
	font-size: 0;
	position: relative;
	label {
		font-size: 12px;
	}
}
.form-label {
	display: inline-block;
	text-align: right;
	font-size: 12px;
	flex-basis: initial;
	flex-grow: 0;
}
.signin-form {
	position: relative;
	width: 30rem;
	margin: 0 auto;
	padding: 1rem 0;
	background-color: #f7f7f7;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
	fieldset {
		border: none;
	}
	.password-set {
		position: relative;
		.forget-pwd-link {
			position: absolute;
			top: 1.8rem;
			right: 1rem;
		}
	}
	.remember-checkbox {
		display: inline-block;
    width: initial;
    height: initial;
	}
}

.signup-form {
	position: relative;
	width: 30rem;
	margin: 0 auto;
	padding: 1rem 0;
	background-color: #f7f7f7;
	box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
	fieldset {
		border: none;
	}
	input {
		width: 100%;
		line-height: 3rem;
		height: 3rem;
		border: 1px solid #999;
		padding: 0 1rem;
	}
}
.form-extra {
	font-size: 12px;
	display: inline-block;
	padding: 0 0 0 1rem;
	position: absolute;
	left: 40rem;
	top: 0;
    white-space: nowrap;
}
.btn-submit {
	display: block;
	font-size: 12px;
	margin: 0 auto;
}
.extra {
	text-align: center;
}
.users-list {
	margin: 0 auto;
    width: 50rem;
}
.gravatar_edit {
	text-align: center;
	.image-upload {
		input {
			@extend %fileupload-btn;
		}
	}
}
.s-avatar {
	font-size: 0;
}

.userName {
	padding: 0 .2rem;
}
.email {
	@extend .userName;
}
.show-link {
	margin-left: .5rem;
}
.show-link + .edit-link {
	margin-left: .5rem;
}
.edit-link + .delete-user {
	margin-left: .5rem;
}
.tweet-box[contenteditable="true"] {
	outline: none;
    border: 1px solid rgba(74, 145, 60, 0.25);
    padding: 8px 10px;
    color: #333;
    &.showPlaceholder {
	    &:before {
		    content: attr(data-placeholder-default);
		    color: #ccd6dd;
		    position: absolute;
		}
    }
}